@import '~antd/es/style/themes/default.less';

.registerContainer {
    width: 100%;
    height: 100%;
    .register {
        padding-top: 200px;
        width: 368px;
        margin: 0 auto;
        :global {
            .ant-tabs  {
                .ant-tabs-nav{
                    margin: 0 auto;
                    margin-bottom: 24px;
                }
                &:before{
                    border-bottom: 0;
                }
            }
        }
    }
    .icons {
        margin-left: 16px;
        color:@text-color-secondary;
        font-size: 24px;
        vertical-align: middle;
        cursor: pointer;
        transition: color 0.3;
        &:hover{
            color: @primary-color;
        }
    }
    .link {
        float: right;
    }
}